          <div class="name">{{m.name}}</div>
          <div class="accounts">
            <a :href="`//weibo.com/${m.weibo}`" :title="`-${m.name}`" target="_blank" class="favicon favicon-weibo" v-if="m.weibo"></a>
            <a :href="`//space.bilibili.com/${m.bilibili}`" :title="`B-${m.name}`" target="_blank" class="favicon favicon-bilibili" v-if="m.bilibili"></a>
            <!-- <a href="javascript:void(0)" :data-clipboard-text="m.douyin" :title="`${m.name}-${m.douyin}`" target="_blank" class="favicon favicon-douyin" v-if="m.douyin"></a> -->
            <a href="javascript:void(0)" :title="`${m.name}-${m.douyin}`" target="_blank" class="favicon favicon-douyin" v-if="m.douyin"></a>
            <a :href="m.changba" :title="`-${m.name}`" target="_blank" class="favicon favicon-changba" v-if="m.changba"></a>
            <a :href="m.miaopai" :title="`-${m.name}`" target="_blank" class="favicon favicon-miaopai" v-if="m.miaopai"></a>
            <a :href="m.weibomusic" :title="`-${m.name}`" target="_blank" class="favicon favicon-music-weibo" v-if="m.weibomusic"></a>
          </div>
        </div>
      </div>
    </div>
    <div class="block">
      <h2></h2>
      <div class="members">
        <div class="member" v-for="m in pet48DataList" :key="m.name">
          <div class="photo" :style="`background-image:url('./static/pet48/${m.name}.jpg')`">  
            <div class="cover"></div>
          </div>
          <div class="name">{{m.master}} - {{m.name}}</div>
        </div>
      </div>
    </div>    
    <div class="block">
      <h2></h2>
      <div class="members">
        <div class="member" v-for="m in otherTeamMemberDataList" :key="m.name">
          <div class="photo" :style="`background-image:url(${m.photo})`">  
            <a v-if="m.group" class="group name" :class="m.group" target="_blank" :href="getGroupWeibo(m.group)"></a>
            <div class="cover"></div>
          </div>
          <div class="name">{{m.name}}</div>
          <div class="accounts">
            <a :href="`//weibo.com/${m.weibo}`" :title="`-${m.name}`" target="_blank" class="favicon favicon-weibo" v-if="m.weibo"></a>
            <a :href="`//space.bilibili.com/${m.bilibili}`" :title="`B-${m.name}`" target="_blank" class="favicon favicon-bilibili" v-if="m.bilibili"></a>
            <!-- <a href="javascript:void(0)" :data-clipboard-text="m.douyin" :title="`${m.name}-${m.douyin}`" target="_blank" class="favicon favicon-douyin" v-if="m.douyin"></a> -->
            <a href="javascript:void(0)" :title="`${m.name}-${m.douyin}`" target="_blank" class="favicon favicon-douyin" v-if="m.douyin"></a>
            <a :href="m.changba" :title="`-${m.name}`" target="_blank" class="favicon favicon-changba" v-if="m.changba"></a>
            <a :href="m.miaopai" :title="`-${m.name}`" target="_blank" class="favicon favicon-miaopai" v-if="m.miaopai"></a>
            <a :href="m.weibomusic" :title="`-${m.name}`" target="_blank" class="favicon favicon-music-weibo" v-if="m.weibomusic"></a>
          </div>
        </div>
      </div>
    </div>
    <div class="block">
      <h2></h2>
      <div class="members">
        <div class="member" v-for="m in exitMemberDataList" :key="m.name">
          <div class="photo" :style="`background-image:url(${m.photo})`">  
            <a v-if="m.group" class="group name" :class="m.group" target="_blank" :href="getGroupWeibo(m.group)"></a>
            <div class="cover"></div>
          </div>
          <div class="name">{{m.name}}</div>
          <div class="accounts">
            <a :href="`//weibo.com/${m.weibo}`" :title="`-${m.name}`" target="_blank" class="favicon favicon-weibo" v-if="m.weibo"></a>
            <a :href="`//space.bilibili.com/${m.bilibili}`" :title="`B-${m.name}`" target="_blank" class="favicon favicon-bilibili" v-if="m.bilibili"></a>
            <!-- <a href="javascript:void(0)" :data-clipboard-text="m.douyin" :title="`${m.name}-${m.douyin}`" target="_blank" class="favicon favicon-douyin" v-if="m.douyin"></a> -->
            <a href="javascript:void(0)" :title="`${m.name}-${m.douyin}`" target="_blank" class="favicon favicon-douyin" v-if="m.douyin"></a>
            <a :href="m.changba" :title="`-${m.name}`" target="_blank" class="favicon favicon-changba" v-if="m.changba"></a>
            <a :href="m.miaopai" :title="`-${m.name}`" target="_blank" class="favicon favicon-miaopai" v-if="m.miaopai"></a>
            <a :href="m.weibomusic" :title="`-${m.name}`" target="_blank" class="favicon favicon-music-weibo" v-if="m.weibomusic"></a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import WordCloud from 'components/plugin/highchart-wordcloud'
  import {memberData, pet48Data, exitMemberData, otherTeamMemberData} from 'config/memberData'

  export default {
    name: 'Index',
    components: {
      WordCloud
    },
    data () {
      const text = 'Team SII,Team SII,,,,,,,,,,,,,,,,,,,,,,,,,48,Mad World,,,,,,,,,,,48,,,,,,,,Mute,,,Go away,UZA,,,mammoth,,,,,,,16,,,,,,,,,,,,,,109,,SNH,,,,,,,,,,,Kiss,,!S,,,,,,,,,,,,,,,,,,,,,,,,Bye Bye Bye,,,,,,,,,,,,,,,,,,,,,,,Drop It,Good Time,,,,,,,Burning Up,La La La,,,,,,,,,,,,Who is the one,Forever XLVIII,Lost,Hold me tight,,,,Somewhere,,Restart,,,, 121E 31N'
      const test2 = ',,,,,SPY,,9 to 9,,,My boy,,,Don\'t Touch,Monster,'
      const test3 = ',,,,'
      return {
        slogan: 'We Are The Best Team SII',
        wordsId: 'wordcloud-container',
        wordsOptions: {
          credits: { enabled: false },
          exporting: { enabled: false },
          series: [{
            type: 'wordcloud',
            data: this.initData(text + test2)
          }],
          title: {
            text: ''
          },
          tooltip: {
            enabled: false
          }
        },
        memberDataList: memberData,
        pet48DataList: pet48Data,
        exitMemberDataList: exitMemberData,
        otherTeamMemberDataList: otherTeamMemberData
      }
    },
    methods: {
      initData (text) {
        return text.split(',').reduce(function (arr, word) {
          var obj = arr.find(function (obj) {
            return obj.name === word
          })
          if (obj) {
            obj.weight += 1
          } else {
            obj = {
              name: word,
              weight: 1
            }
            arr.push(obj)
          }
          return arr
        }, [])
      },
      getGroupWeibo (group) {
        switch(group){
          case 'sense':
            return '//weibo.com/u/5901077408'
          case 'blueV':
            return '//weibo.com/u/6386595576'
          case 'new':
            return 'javascript:void(0)'
          default:
            return 'javascript:void(0)'
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.favicon
  background-repeat no-repeat
  background-position center center
  background-size cover
  &.disable
    filter grayscale(1)
    cursor not-allowed
    pointer-events none
.favicon-weibo
  background-image url('//weibo.com/favicon.ico')
.favicon-bilibili
  background-image url('//www.bilibili.com/favicon.ico')
.favicon-douyin
  background-image url('//www.douyin.com/favicon.ico')
.favicon-changba
  background-image url('//changba.com/favicon.ico')
.favicon-miaopai
  background-image url('//www.miaopai.com/favicon.ico')
.favicon-music-weibo
  background-image url('//image2.sina.com.cn/music/web/logo/weibo2012/logo_16.ico')
#wordcloud-container
  height 400px
  width 100%
  background-color white

.slogan
  text-align center
  color #87cefa
  font-family sans-serif
  font-weight 600

.block
  h2
    text-align center
    font-size 24px
    font-family 'Lobster', serif
  .members
    display flex
    flex-wrap wrap
    justify-content space-between
    align-content space-between
    .member
      .name
        text-align center
      .photo
        height 160px
        background-repeat no-repeat
        background-position center center
        background-size contain
        position relative
        .group
          position absolute
          left calc(50% + 37.5px)
          top -12.5px
          border-radius 50%
          background-repeat no-repeat
          background-position center center
          background-size contain
          display inline-block
          width 40px
          height 40px
          box-shadow 0 0 3px #000
        .sense
          background-image url('/TeamSII/dist/static/images/sense.jpg')
        .blueV
          background-image url('/TeamSII/dist/static/images/bluev.jpg')
        .new
          background-image url('/TeamSII/dist/static/images/new.png')
        .cover
          background-image url('http://www.snh48.com/images/member/zx5_def_s.png')
          background-repeat no-repeat
          background-position center center
          background-size contain 
          width 100%
          height 100%
      .accounts
        margin 0 auto
        display flex
        justify-content space-around
        .favicon
          flex-grow 0
          flex-shrink 1
@media screen and (min-width 1366px)
  .slogan  
    font-size 24px
  .favicon
    width 16px
    height 16px
  .block
    h2
      margin 0 0 16px 0
    .members
      .member
        flex 0 0 20%
        margin-bottom 25px
      .name
        font-size 24px
        margin 10px auto
      .accounts
        width 60%
@media screen and (max-width 1366px)
  .slogan  
    font-size 20px
  .favicon
    width 16px
    height 16px  
  .block
    h2
      margin 0 0 12px 0
    .members
      .name
        font-size 20px
        margin 8px auto
      .member
        flex 0 0 25%      
        margin-bottom 20px
      .accounts
        width 65%
@media screen and (max-width 700px)
  .slogan  
    font-size 16px
  .favicon
    width 16px
    height 16px
    
  .block
    h2
      margin 0 0 8px 0
    .members
      .name
        font-size 16px
        margin 6px auto
      .member
        flex 0 0 33%      
        margin-bottom 15px
      .accounts
        width 70%
@media screen and (max-width 540px)
  .slogan  
    font-size 12px
  .favicon
    width 16px
    height 16px    
  .block
    h2
      margin 0 0 4px 0
    .members
      .name
        font-size 12px
        margin 4px auto
      .member
        flex 0 0 50%
        margin-bottom 10px
      .accounts
        width 75%
</style>



